<template>
  <div class="container">
    <h2>HMAC - {{ name }}</h2>
    <p>
      {{ about }}
    </p>
    <div class="ui card">
      <div class="content">
        <h4 class="ui sub header">Message String</h4>
        <div class="ui input">
          <textarea
            placeholder="Enter Message..."
            rows="6"
            spellcheck="false"
            v-model="input"
          ></textarea>
        </div>

        <h4 class="ui sub header">Secret Passphrase</h4>
        <div class="ui input">
          <textarea
            placeholder="Enter Secret..."
            rows="6"
            spellcheck="false"
            v-model="secret"
          ></textarea>
        </div>

        <h4 class="ui sub header">HMAC - {{ name }} Output</h4>
        <div class="ui input">
          <textarea
            id="output"
            placeholder=""
            rows="6"
            spellcheck="false"
            v-model="output"
          ></textarea>
        </div>
      </div>
      <div class="extra content">
        <button class="ui primary button" @click="generate">
          Generate
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import CryptoJS from 'crypto-js';

export default {
  props: {
    name: { type: String, required: true },
    about: { type: String, default: '' },
  },
  data() {
    return {
      input: '',
      secret: '',
      output: '',
    };
  },
  methods: {
    generate() {
      const target = CryptoJS[`Hmac${this.name}`];
      if (!target) return;

      const hash = target(this.input, this.secret);
      this.output = `${hash}`;
    },
  },
};
</script>

<style scoped>
.ui.card {
  width: 100%;
}

.ui.input,
.ui.input textarea {
  width: 100%;
}

.ui.input textarea {
  resize: vertical;
  padding: 0.5rem;
}
</style>
