<template>
  <div>
    <sui-menu pointing secondary class="option-menu">
      <a
        is="sui-menu-item"
        v-for="item in options"
        :active="isActive(item.value)"
        :key="item.value"
        :content="item.text"
        @click="select(item.value)"
      />
    </sui-menu>
    <sui-dropdown fluid selection :options="options" v-model="current" class="option-dropdown" />

    <div class="ui segment">
      <div v-if="current === 'md5'">
        <HmacMD5 />
      </div>
      <div v-else-if="current === 'sha1'">
        <HmacSHA1 />
      </div>
      <div v-else-if="current === 'sha256'">
        <HmacSHA256 />
      </div>
      <div v-else-if="current === 'sha512'">
        <HmacSHA512 />
      </div>
      <div v-else></div>
    </div>
  </div>
</template>

<script>
import HmacMD5 from '@/components/HmacMD5.vue';
import HmacSHA1 from '@/components/HmacSHA1.vue';
import HmacSHA256 from '@/components/HmacSHA256.vue';
import HmacSHA512 from '@/components/HmacSHA512.vue';

export default {
  name: 'CryptoHMAC',
  components: {
    HmacMD5,
    HmacSHA1,
    HmacSHA256,
    HmacSHA512,
  },
  data() {
    return {
      current: 'md5',
      options: [
        {
          text: 'MD5',
          value: 'md5',
        },
        {
          text: 'SHA1',
          value: 'sha1',
        },
        {
          text: 'SHA256',
          value: 'sha256',
        },
        {
          text: 'SHA512',
          value: 'sha512',
        },
      ],
    };
  },
  methods: {
    isActive(name) {
      return this.current === name;
    },
    select(val) {
      this.current = val;
    },
  },
};
</script>
<style lang="scss">
.option-dropdown {
  display: none !important;
}

@media (max-width: 767.98px) {
  .option-menu {
    display: none !important;
  }
  .option-dropdown {
    display: block !important;
    margin-top: 0.3rem;
  }
}
</style>
